<script>
import { Line } from 'vue-chartjs'
import axios from 'axios'

export default {
    extends: Line,
    name: 'RevenueHourlyVariationChart',
    data () {
        return {
            chartLabels: {
                type: Array,
                required: true
            },
            chartData: {
                type: Array | Object,
                required: false
            },
            options: {
                scales: {
                    yAxes: [{
                        ticks: {
                            beginAtZero: true
                        },
                        gridLines: {
                            display: true
                        }
                    }],
                    xAxes: [ {
                        gridLines: {
                            display: false
                        }
                    }]
                },
                legend: {
                    display: true
                },
                responsive: true,
                maintainAspectRatio: false,
            }
        }
    },

    created() {
        const baseURI = 'https://api.eosknights.io';
        axios.post(`${baseURI}/api/v1/revenue/revenues/hourly-variations`,{
            from: 0,
            size: 1000
        }).then((result) => {
                //console.log(result)
                const labels=[]
                const userCounts=[]
                const itemIventoryUps=[]
                const itemTaxes=[]
                const knights=[]
                const matIventoryUps=[]
                const materialTaxes=[]
                const mps=[]
                const items = result.data
                for(var key in items) {
                    const item = items[key]
                    labels.push(item.created)
                    userCounts.push(item.userCount)
                    itemIventoryUps.push(item.itemIventoryUp)
                    matIventoryUps.push(item.matIventoryUp)
                    itemTaxes.push(item.itemTax)
                    materialTaxes.push(item.materialTax)
                    knights.push(item.knight)
                    mps.push(item.mp)
                }

                this.chartLabels = labels
                this.chartData = [
                    {
                        label: 'GitHub Commits',
                        backgroundColor: '#f87979',
                        data: userCounts
                    }
                ]
                this.renderChart({
                    labels: labels,
                    datasets: [{
                            label: '가입자수',
                            backgroundColor: 'rgba(242, 11, 8, 0.2)',
                            borderColor: '#f20b08',
                            data: userCounts
                        }, {
                            label: '매직워터구매',
                            backgroundColor: 'rgba(235, 132, 8, 0.2)',
                            borderColor: '#eb8408',
                            data: mps
                        }, {
                            label: '기사구매',
                            backgroundColor: 'rgba(227, 218, 8, 0.2)',
                            borderColor: '#e3da08',
                            data: knights
                        }, {
                            label: '재료 수수료',
                            backgroundColor: 'rgba(7, 218, 8, 0.2)',
                            borderColor: '#07da08',
                            data: materialTaxes
                        }, {
                            label: '아이템 수수료',
                            backgroundColor: 'rgba(42, 125, 39, 0.2)',
                            borderColor: '#2a7d27',
                            data: itemTaxes
                        }, {
                            label: '재료인벤 구입',
                            backgroundColor: 'rgba(7, 218, 218, 0.2)',
                            borderColor: '#07dada',
                            data: matIventoryUps
                        }, {
                            label: '아이템인벤 구입',
                            backgroundColor: 'rgba(42, 125, 211, 0.2)',
                            borderColor: '#2a7dd3',
                            data: itemIventoryUps
                        },
                    ]
                }, this.options)

            })

    },
}

</script>




